import React from "react";

export default function Step({isActive, headings, component}) {

    const ClonedElementWithMoreProps = React.cloneElement(
        component,
        { headings: headings }
    );

    return(
        <div className={`acpt-step ${isActive ? 'active': 'hidden'}`}>
            {ClonedElementWithMoreProps}
        </div>
    )
}